* {
    padding: 0;
    margin: 0;
    user-select: none;
    -webkit-tap-highlight-color: transparent;
}


body {
    background-color: rgba(153, 255, 158, 0.918);
}

.Dashboard {
    background-color: rgb(33, 49, 78);
    height: 8.5vh;
    border-radius: 40px 40px 0px 0px;
    display: flex;
    padding: 2px;
    justify-content: center;
    position: fixed;
    bottom: 0;
    width: 100%;
}

.btn-box {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.dashboard-btn {
    margin: 0px 30px;
    background-color: rgba(255, 255, 255, 0);
    border: none;
    outline: none;
}

.displayBox {
    background-color: rgb(255, 255, 255);
    border-radius: 38px 38px 0px 0px;
    position: fixed;
    bottom: 0;
    overflow: scroll;
    /* overflow: hidden; */
    height: 62%;
    width: 100%;
    min-height: 45vh;
    display: flex;
    flex-direction: column;
    /* justify-content: start; */
    padding-bottom: 10vh;
    padding-top: 3vh;
}

.displayBox::-webkit-scrollbar {
    width: 0;
    height: 0;
}

.topDisplayBox {
    /* margin-top: 4px; */
    margin: 6px 6px;
}

.icon-box {
    display: flex;
    /* align-items: center; */
    justify-content: space-between;
}

.budget-box {
    margin: 30px 0px;
}

.budget-box h5 {
    margin: 0px 7px;
    display: block;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 1.2rem;

}

.budget-box h2 {
    display: block;
    margin: 7px;
    font-size: 3.8rem;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.item-box {
    display: flex;
    margin: 2px 19px;
    height: max-content;
    padding: 10px;
    font-size: 1.5em;
    justify-content: space-between;
}

.log-box {
    display: flex;
    margin: 7px 22px;
    height: max-content;
    /* background-color: rgb(220, 255, 255); */
    border-top: 1px solid rgb(44, 61, 112);
    border-bottom: 1px solid rgb(44, 61, 112);
    /* border-left: 1px solid rgb(44, 61, 112); */
    flex-direction: column;
    border-radius: 10px;
}

.log-detail-box {
    padding: 8px;
    display: flex;
    justify-content: space-between;
}

.TransAmnt {
    color: rgb(255, 106, 19);
    font-weight: bold;
    font-family: 'Gill Sans MT', sans-serif;

}

.TransTitle {
    font-size: 1.35rem;
    font-family: 'Gill Sans MT', sans-serif;
}

.addModal {
    /* display: flex; */
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* border: 2px solid rgb(202, 202, 202); */
    margin: 0px 10px;
}

.amountInp {
    margin: 1px 20px;
    border: none;
    border-bottom: 2px solid rgba(49, 49, 49, 0.452);
    /* border-radius: 10px; */
    padding: 12px;
    font-size: 1.2em;
    /* min-width: fit-content; */
    max-width: 7.8em;

}

.amountInp:focus {
    outline: none;
    border-bottom: 2px solid rgb(58, 58, 58);

}

.titleInp {
    border: none;
    border-bottom: 2px solid rgba(49, 49, 49, 0.452);
    margin: 1px 30px;
    padding: 12px;
    max-width: 7.8em;
    font-size: 1.2em;
    /* max-width:; */
}

.titleInp:focus {
    outline: none;
    border-bottom: 2px solid rgb(58, 58, 58);
}

.titleBox {
    display: flex;
    align-items: center;
    flex-direction: row;
}

.dateBox {
    display: flex;
    align-items: center;
    flex-direction: row;
}

.amountBox {
    display: flex;
    align-items: center;
    flex-direction: row;

}

.ruppeSymbol {
    font-size: 1.9em;
    background-color: rgba(93, 230, 29, 0.918);
    padding: 5px;
    border-radius: 10px;
    width: 60px;
    text-align: center;
    cursor: pointer;

}

.ruppeSymbol-red {
    font-size: 1.9em;
    background-color: rgba(224, 47, 47, 0.856);
    padding: 5px;
    border-radius: 10px;
    width: 60px;
    text-align: center;
    cursor: pointer;

}

.submit-btn-addModal {
    padding: 15px;
    outline: none;
    border: none;
    border-radius: 10px;
    background-color: rgba(153, 255, 158, 0.918);
    margin: 40px 100px;
}

.AddTrans--Heading {
    text-align: center;
    text-transform: uppercase;
    font-family: 'Lucida Sans', sans-serif;
    margin-bottom: 15px;
}

.transName-addModal {
    margin-top: 25px;
    font-size: 1.28rem;
}

.transDate-addModal {
    margin-top: 25px;
    font-size: 1.28rem;
}

.userAcc-heading {
    text-transform: uppercase;
    letter-spacing: 0.5px;
    text-align: center;
    font-family: 'Lucida Sans', sans-serif;

}

.logs-box {
    border-top: 2px solid rgba(161, 157, 165, 0.377);
    margin: 30px;
}

.logs-box-heading {
    text-transform: uppercase;
    letter-spacing: 0.2px;
    text-align: center;
    margin: 15px;
    font-size: 18px;
    font-family: 'Lucida Sans', sans-serif;
}

.logs-box-details {
    margin: 20px;
    display: flex;
    flex-direction: column;
    /* justify-content: space-between; */

}

.box-log-detail {
    margin-bottom: 8px;
    /* text-transform: uppercase; */
    font-family: 'Lucida Sans', sans-serif;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.underDev {
    text-align: center;
    color: red;
}

.dlt-all-trans-btn {
    color: white;
    outline: none;
    border: none;
    width: 55px;
    padding: 7px;
    cursor: pointer;
    border-radius: 8px;
    background-color: rgba(255, 28, 28, 0.795);
}

.addTrans-Form {
    /* border: 2px solid red; */
    margin: 20px;
}

.Trans-date-Input {
    margin-top: auto;
    padding: 5px;
    margin-left: 30px;
    width: 9.5em;
    font-size: 1.1rem;
    outline: none;
    border: none;
    background-color: rgb(255, 255, 255);
    border-bottom: 2px solid rgba(150, 150, 150, 0.829);
}

.welcm-box {
    /* border: 2px solid red; */
    margin: 20px;
    font-size: 1.2rem;
    text-align: center;
    font-family: 'Lucida Sans', Verdana, sans-serif;
}

.wlcm-heading {
    margin-top: 20px;
}

.go-to-add-btn {
    border: 2px solid rgb(255, 255, 255);
    text-align: center;
    width: 30%;
    padding: 8px;
    color: white;
    border-radius: 17px;
    background-color: rgba(0, 0, 0, 0.836);
    margin: auto;
}

.add-btn-wlcmBox {

    margin-left: 5px;
    margin-bottom: -9px;
}

input[type="file" i]::-webkit-file-upload-button {
    color: rgb(0, 0, 0);
    outline: none;
    border: none;
    padding: 10px;
    cursor: pointer;
    border-radius: 8px;
    background-color: rgba(27, 89, 204, 0.925);
    color: white;
}

input[type="file" i] {
    color: rgba(255, 255, 255, 0);
    border-radius: 12px;
    max-width: 87px;
}

.inpFileBox {
    display: flex;
    justify-content: center;
}

.ExpFileBox {
    display: flex;
    justify-content: center;
    margin-bottom: 7px;
}

.txt-center {
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
}

.backUP-btn {
    border: none;
    background-color: rgba(27, 89, 204, 0.925);
    color: white;
    border-radius: 12px;
    padding: 10px;
    max-width: 87px;
}

.blu-btn-color {
    background-color: rgba(27, 89, 204, 0.925);
}

#userNameInput {
    display: none;
    justify-content: end;
}

#userName-edit-input {
    max-width: 40%;
    border: none;
    border-bottom: 2px solid gray;
    padding: 6px;
}

#userNameInput {
    margin-left: 10px;
}

#userName-edit-input:focus {
    outline: none;
    background-color: none;
}


.curvedGreetBox {
    background-color: rgb(33, 49, 78);
    color: white;
    font-weight: 800;
    font-family: Arial, Helvetica, sans-serif;
    letter-spacing: 0.23px;
    padding: 12px;
    /* text-transform: uppercase; */
    border-radius: 16px;
    max-width: max-content;
}

.item-selector {
    display: flex;
    justify-content: center;
}

.selector-btn {
    outline: none;
    border: none;
    padding: 8px;
    width: 90px;
    cursor: pointer;
    font-size: 1.1em;
    background-color: rgba(33, 49, 78, 0.774);
    color: white;
    margin-bottom: 2.7vh;
}

.selec-left-btn {
    border-radius: 16px 0px 0px 16px;
    background-color: rgba(33, 49, 78, 0.932);

}

.selec-mid-btn {
    margin: 0px 0.2px;
}

.selec-right-btn {
    border-radius: 0px 16px 16px 0px;

}


/* Responsive */
@media only screen and (min-width: 775px) {
    .displayBox {
        width: 45%;
    }

    .full-body-containerBox {
        display: flex;
        justify-content: center;
        align-items: center;
        justify-content: center;
    }

    .Dashboard {
        width: 45%;
    }
}

@media only screen and (max-height: 560px) {

    .budget-box h2 {
        font-size: 2.8rem;
    }
}

@media only screen and (max-height: 475px) {
    .Dashboard {
        height: 20.5vh;
    }

    .displayBox {
        min-height: 83vh;
    }

    .icon-box {
        margin-left: -400px;
        flex-direction: column-reverse;
    }

    .displayBox {
        padding-bottom: 11.5vh;
    }
}